.style-selector{
  height: 100%;
  width: 297px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100000;
  box-shadow: -3px 0px 50px -2px rgba(0, 0, 0, 0.14);
  background: #fff;
}
.style-selector1{
    position: fixed;
    top: 0;
    width: 297px;
    right: -297px;
    z-index: 100000;
    box-shadow: -3px 0px 50px -2px rgba(0, 0, 0, 0.14);
    background: #fff;
  }
  
#style-selector-container{
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;

}
.style-selector-hide{
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all 0.5s;
    width: -297px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 100000;
    box-shadow: -3px 0px 50px -2px rgba(0, 0, 0, 0.14);
    background: #fff;
}
.ss-content-first{
    padding-top: 30px;
    padding-bottom: 0;
    border: none;
    margin: 0 30px 30px;
}
.ss-toggle{
    width: 52px;
    height: 56px;
    cursor: pointer;
    opacity: 1;
    background: #fff;
    box-shadow: -3px 0px 5px -2px rgba(0, 0, 0, 0.14);
    float: left;
    margin-left: -52px;
    margin-top: 150px;
    top: 100px;
}
.ss-toggle::before{
    color: #000000;
    content: '\f013';
    font-family: 'Font Awesome 5 Free';
    font-size: 23px;
    font-weight: 900 !important;
    line-height: 56px;
    text-align: center;
    padding-left: 15px;
}
.ss-buybtn{
    color: #fff;
    width: 100%;
    height: 45px;
    line-height: 45px;
    font-size: 15px;
    border: none;
    background: #a0ce4e;
}
.fushion-btn{
    display: inline-block;
    position: relative;
    zoom: 1;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box;
}
.ss-policy{
    text-align: center;
    font-size: 13px;
   
}
.ss-title{
    font-size: 15px;
    color: #000000;
    text-transform: uppercase;
    text-align: center;
    display: block;
    margin-bottom: 15px;
}
.ss-content{
    border-bottom: 1px solid #dddddd;
    padding-bottom: 30px;
    margin: 0 30px 30px
}
.ss-wide-button{
    
    color: #000000;
    display: inline-block;
    width: 48%;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
}
.ss-layout-button{
    text-align: center;
    background: #dddddd;
    margin-right: 2%;
}
.ss-boxed-button{
    color: #000000;
    display: inline-block;
    width: 48%;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
}
.ss-button{
    font-weight: normal;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}
.images{
    text-align: center;
}
.images .bkgd1 {
    background-position: 0px 0;
