.n-content4{
    background-color: #f6f6f6;
    background-position: left top;
    background-repeat: no-repeat;
    
}
.n-content4-wrapper{
    height: 250px;

}

.n-content-title1 , .n-content-title{
    text-align: center;
    padding-top: 10px;
}

.n-content4-c1, .n-content4-c2 ,.n-content4-c3{
    
    display: inline-block;
    width: 30%;
    background-color: #ffffff;
    margin-left: 20px;
    margin-right: 20px;
    height: 175px;
    
}
.n-content-btn{
    text-align: center;
    color: white;
    padding-right: 40px;
    padding-top: 30px;
}
.btn-style{
    padding: 17px 40px;
    
    background-color: #A0CE4E;
}
.n-content5{
    background-color: #A0CE4E;
    height: 119px;
   
}
.btn-content5{
    border: 2px solid white;
  padding: 17px 40px;
  line-height: 21px;
  font-size: 18px;
}
.n-content5-content{
    text-align: center;
    color: white;
    padding-top: 50px;
}
.n-content5-detail{
    display: inline-block;
    border-color: white;
    border-width: 2px;
}
.n-content6-text{
    text-align: center;
    font-size: 30px;
    margin-bottom: 20px;
}
.n-content6-wapper{
    margin-top: 50px;
}
.n-content6{
    height: 250px;
    background-color: #ffffff;
}
.n-content6-tdesc{
    text-align: center;
}
