.next-layout-container{
    background-color: rgba(255,255,255,0);
    background-image: url(https://avada.theme-fusion.com/wp-content/uploads/2015/04/block_bkgd2.jpg);
    background-position: center center;
    background-repeat: repeat;
    padding-top: 155px;
    padding-right: 30px;
    padding-bottom: 120px;
    padding-left: 30px;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-color: #eae9e9;
    border-top-style: solid;
    border-bottom-style: solid;
    position: relative;
  
}
.fushion-builder-row{
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    margin: 0 auto;
}
.fushion-row{
    position: relative;
    z-index: 10;
}
.next-layout-wrapper{
    margin-top: 0px;
    margin-bottom: 3.5%;
    width: 30.6666%;
    
}
.fusion-layout-column {
    position: relative;
    float: left;
    margin-bottom: 20px;
}
.next-layout-content{
    background-color: #ffffff;
    padding: 0px 0px 0px 0px;
    background-position: left top;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
.layout-img{
    text-align: center;
    overflow: hidden;
}
.layout-img-detail{
    border: 1px solid rgba(000,000,000,.06);
    display: inline-block;
    z-index: 1;
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    max-width: 100%;
}
.layout-text{
    text-align: center;
   margin-top: 10px;
   padding-bottom: 10px;
    font-size: 18px;
    line-height: 27px;
}
.img-responsive{
    width: 461px;
    height: 315px;
}
.layout-details{
    background-color: #ffffff;
    padding: 0px 0px 0px 0px;
    background-position: left top;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
.layout-img:hover img{
    transform: scale(1.2);
    transition-duration: 0.8s;
}