.slidingbar {
    background-color: #363839;
}
.s-title{
    color: white;
    font-size: 20px;
    
}
.img-item-work{
    display: inline-block;
    margin-right: 30px;
    margin-top: 20px;
}
.toggle-button{
    border-top: 36px solid #363839;
    border-left: 36px solid transparent;
    border-top-color: #363839;
    display: block;
    position: relative;
    transition: none;
    pointer-events: auto;

}
