.toggle-button::after{
    display: block;
    position: absolute;
    content: '+';
    font-family: icomoon;
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    transition: none;
    font-size: 16px;
    left: -15px;
    top: -33px;
    color: #ffffff;
}
.sign-toggle{
    color: #ffffff;
   
}
.slidingbar-top-bar{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    
}
.sliding-top-bar-area{
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-justify-content: flex-end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    z-index: 99999;
    margin: 0;
    pointer-events: none;
    transition: all .4s ease;
    color: #8c8989;
}
.sliding-row{
    margin: 0 auto;
    zoom: 1;
}
.sliding-toggle{
    display: block;
    position: relative;
    
    width: 36px;
    height: 36px;
    
}
