.expand{
    
    padding-top: 20px;
    padding-right: 40px;
    padding-bottom: 35px;
    padding-left: 80px;
    background-color: #363839;
    height: 300px;
    transition:  0.6s  ease-in;
    transition-duration: 0.4s;
}
.none-expand{
    height: 0;
    padding: 0;
    transition:  0.6s  ease-out;
}
.toggle-wrapper{
    width: 36px;
    height: 36px;
    
    display: block;
    position: relative;
}
.screen-reader-text {
    border: 0;
    clip: rect(1px,1px,1px,1px);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute!important;
    width: 1px;
    word-wrap: normal!important;
}
.fusion-sb-toggle {
    display: block;
    position: relative;
    transition: none;
    pointer-events: auto;
}
.sliding-text .contact-info-container{
    color: #747477;
    word-break: break-all;
  
}
.img-work{
    text-align: center;
}
 a {
    color: #bfbfbf;
 }
