.n-content{
    background-color: rgba(255,255,255,0);
    background-position: left top;
    background-repeat: no-repeat;
    padding-top: 60px;
    padding-right: 30px;
    padding-bottom: 0px;
    padding-left: 30px;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-color: #eae9e9;
    border-top-style: solid;
    border-bottom-style: solid;
    background: #fff;
}
.n-content-des{
    text-align: center;
}
.n-desc-num{
    color: #a0ce4e;
}
img{
    border-style: none;
    vertical-align: top;
    max-width: 100%;
    height: auto;
}
.n-cont-imageP{
    width: 1070px;
    height: 520px;
}
.f-imageframe{  
    z-index: 1;
    overflow: hidden;
    position: relative;
    
    text-align: center;
    max-width: 100%
} 
.n-content-holding{
    text-align: center;
}
.fusion-imageframe {
    
    z-index: 1;
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    max-width: 100%;
}
.n-desc-details{
    text-align: center;
    font-size: 18px;
    line-height: 29px;
}