.parallax-inner{
    pointer-events: none;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: 0px;
    left: 0px;
    opacity: 1;
    background-position: 0px 0px;
    background-repeat: repeat;
    background-size: auto;
    background-image: url(https://avada.theme-fusion.com/wp-content/uploads/2014/09/avada_collage_design21.jpg);
    background-color: rgba(255, 255, 255, 0);
    background-blend-mode: overlay;
}
