.sub-menu{
    position: relative;
    display: block;
    left: auto;
    right: auto;
    border: none;
    background: 0 0;
    box-shadow: none;
    top: 0;
    width: auto;
    padding: 0;
}

.nav-logo {
   display: inline-block;
}
.nav-row{
    background-color: #ffffff;
    max-width: 1100px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 30px;
    margin: 0 auto;
    zoom: 1;
   
}
.nav-header{
    background-color: #ffffff;
    height: 89px;
    top: 0px;
   
    position: fixed;
    width: 100%;
}
.header-wraper{
    position: relative;
    z-index: 10010;
}
