.them-container{
    background-color: rgba(255, 255, 255, 0);
    background-image: none;
    background-position: left top;
    background-repeat: repeat;
    padding: 95px 30px 100px;
    border-top: 0px solid rgb(234, 233, 233);
    border-bottom: 0px solid rgb(234, 233, 233);
    border-right-color: rgb(234, 233, 233);
    border-left-color: rgb(234, 233, 233);
    position: relative;
    overflow: hidden;
    z-index: 1
}
.theme-inner{
    pointer-events: none;
    width: 1334px;
    height: 5966.4px;
    position: absolute;
    z-index: -1;
    top: 0px;
    left: 0px;
    opacity: 1;
    background-position: 0px 0px;
    background-repeat: repeat;
    background-size: auto;
    background-image: url(https://avada.theme-fusion.com/wp-content/uploads/2015/02/avada_collage_design1.jpg);
    background-color: rgba(255, 255, 255, 0);
    background-blend-mode: overlay;
    min-height: 150px;
    transform: translate3d(0px, -8.71744px, 0px);
}
.them-content{
    display: inline-block;

}
.them-content-item{
    text-align: center;
    width: 25%;
    margin-right: 20px;
    background-color: #ffffff;
    min-height: 467px;
}
.them-img{
    padding-top: 35px;
    padding-bottom: 20px;
}
.theme-img-src{
    border: 5px solid #CECECE;
}
.them-text{
    text-align: center;
    font-size: 15px;
    line-height: 26px;
}
.them-btn{
    margin-bottom: 20px;
   
}
.them-content-details{
    display: flex;
    justify-content: center;
}
.btn-border{
    border: 1px solid #333333;
    border-radius: 2px;
    padding: 10px 9px 10px 9px
}

